/* CSS Document */

body {
  background: url('../img/bg.png') repeat-x;
  margin: 0;
  padding: 0;
}

#header_container {
  position: left;
  margin-left: 0px;
}

#page_container {
  display: block;
  background: #fff;
  margin-top: 20px;
  position: relative;
  margin-left: 0px;
}

#controls {
  border: 1px solid black;
}

.ui-dialog-titlebar {
  border: 1px solid black;
  border-bottom: 0;
}

#article_select {
  border-bottom: 1px solid black;
  margin-bottom: 10px;
}

#logbooks_container {
  display: none;
}

#page {
  max-width: 1275px;
}

.tile {
  width: 100px;
  height: 100px;
  display: tile;
  margin: 0px;
  padding: 0 0 0 0;
}

.tile_wrapper {
  margin: 0px;
  position: absolute;
  display: tile;
  width: auto;
  height: auto;
}

@media (min-width:200px) {
  #header_container {
    width: 200px;
    height: 160px;
  }
  #page_container {
    height: 260px;
